<script>
import filter from 'lodash/filter'
export default {
    name: 'lsautocomplete',
    props: {
        dataList: {type: Array, required: true},
        searchableKeys: {type: Array, default: ['name','title']},
        showKey: {type: String, default: 'name'},
        valueKey: {type: String|Boolean, default: false},
        matchType: {type: String, default: 'fuzzy'},
        itemClass: {type: String, default: ''},
        inputClass:  {type: String, default: ''},
        value: {default: ''}
    },
    data(){
        return {
            input: '',
        }
    },
    computed: {
        showDropdown() {
            return this.input != '';
        },
        filteredList() {
            filter(this.dataList, (listItem) => {
                return this.searchableKeys.reduce((coll, key) => {
                    if(listItem[key] == undefined) { return coll; }
                    return (coll || this[this.matchType](listItem[key]))
                }, false);
            });
        }
    },
    methods: {
        itemSelected(item) {
            const result = this.valueKey===false ? item : item[valueKey];
            this.$emit('change', result);
        },
        fuzzy(comparable) {
            const regExp = new RegExp("%"+this.input+"%");
            return regExp.test(comparable);
        },
        exact(comparable) {
            const regExp = new RegExp(this.input);
            return regExp.test(comparable);
        },
        start(comparable) {
            const regExp = new RegExp(this.input+"%");
            return regExp.test(comparable);
        },
        lazy(comparable) {
            return (comparable.toLowerCase().indexOf(this.input.toLowerCase()) > -1);
        },
    },
    mounted() {
        if(this.value != '') {
            this.input = this.value;
        }
    }
}
</script>

<template>
    <div class="scoped-autocomplete-input-container" :class="itemClass">
        <input type="text" class="form-control" :class="inputClass" v-model="input"/>
        <ul class="scoped-autocomplete-list" v-show="showDropdown">
            <li 
                v-for="(item,i) in filteredList" 
                :key="'autocomplete-'+i"
                @click="itemSelected(item)"
                class="scoped-autocomplete-list-item"
            >
                {{item[showKey]}}
            </li>
        </ul>
    </div>
</template>

<style lang="scss" scoped>
    .scoped-autocomplete-input-container {
        position: relative
    }
    .scoped-autocomplete-list {
        position: absolute;
        top: 36px;
        left:0;
        width: 90%;
        padding:0;
        margin: 4px 5%;
        height:180px;
        overflow: auto;
        border: 1px solid #212121;
        list-style: none;
    }
    .scoped-autocomplete-list-item {
        &:hover { background-color: #dedede; }
        padding: 4px 6px;
        border-bottom: 1px solid #929292;
        &:last-of-type {
            border-bottom: none;
        }
    }
</style>
